import React, { Component } from 'react';
import { Stage, Layer, Text, Image, Rect } from 'react-konva';
import mimi from './mimi.jpeg'

class App extends Component {
  constructor(props) {
    super(props)

    this.state = {
      clientWidth: 0,
      clientHeight: 0,
      image: null,
      areas: [],
      drawingRect: null
    };

    this.store = {
      menuHeight: 80
    };

    // bind this
    this.handleImageChange = this.handleImageChange.bind(this);
    this.handleMouseMove = this.handleMouseMove.bind(this);
    this.hanldeStageClick = this.hanldeStageClick.bind(this);
    this.hanldeDrageFactory = this.hanldeDrageFactory.bind(this);

    this.dragStartPos = null;
  }

  handleImageChange(evt) {
    console.log(evt)
  }

  componentDidMount() {
    this.init()
  }

  init() {
    const clientHeight = window.innerHeight;

    const clientWidth = window.innerWidth;

    this.setState({ clientHeight, clientWidth });

    this.initImage()
  }

  initImage() {
    const image = new window.Image();

    image.onload = () => {
      this.setState({ image })
    };

    image.src = mimi;
  }

  hanldeStageClick(nativeEvent) {
    const { offsetX: x, offsetY: y } = nativeEvent.evt;
    const pos = { x, y }
    console.log('hanldeStageClick', x, y);
    const { drawingRect } = this.state;
    if (drawingRect) {
      this.endDrawOneReact(pos);
    } else {
      this.beginDrawOneRect(pos);
    }
  }

  handleMouseMove(nativeEvent) {
    const { drawingRect } = this.state;
    if (!drawingRect) return;
    const { offsetX: x, offsetY: y } = nativeEvent.evt;
    const pos = { x, y }
    console.log('handleMouseMove', x, y);
    this.drawingOneRect(pos);
  }

  beginDrawOneRect({ x, y }) {
    const drawingRect = {
      start: { x, y },
      end: { x, y }
    };
    this.setState({ drawingRect });
  }

  drawingOneRect({ x, y }) {
    const { drawingRect } = this.state;
    if (!drawingRect) return;
    const { start } = drawingRect;
    this.setState({ drawingRect: { start, end: { x, y } } });
  }

  endDrawOneReact({ x, y }) {
    const { drawingRect, areas } = this.state;
    if (!drawingRect) return;
    const { start } = drawingRect;
    const area = { start, end: { x, y } };
    this.setState({ drawingRect: null,  areas: [...areas, area] });
  }

  hanldeDrageFactory(index) {
    return (nativeEvent) => {
      const { offsetX: x, offsetY: y } = nativeEvent.evt;
      // drag start
      if (!this.dragStartPos) {
        console.log('dragStart', { x, y });
        this.dragStartPos = { x, y }
      } else {
        console.log('dragEnd', { x, y });
        const { x: x0, y: y0 } = this.dragStartPos;
        const offsetX = x - x0;
        const offsetY = y - y0;
        const { areas } = this.state;
        const areasCopy = [...areas];
        const { start, end } = areas[index];
        areasCopy[index] = {
          start: {
            x: start.x + offsetX,
            y: start.y + offsetY
          },
          end: {
            x: end.x + offsetX,
            y: end.y + offsetY
          }
        }
        this.setState({ areas: areasCopy })
        // 重置
        this.dragStartPos = null;
      }
      console.log(index, x, y);
    }
  }

  render() {
    const { clientWidth, clientHeight, image, drawingRect, areas } = this.state;

    const { menuHeight } = this.store;

    const stageHeight = clientHeight - menuHeight;

    return (
      <div style={{height: clientWidth, height: clientHeight}}>
        <div
          style={{
            height: menuHeight,
            width: clientWidth,
            display: 'flex',
            flexDirection: 'row',
            alignItems: 'center',
            background: '#f3f3f3'
          }}
        >
          <input type="file" onChange={this.handleImageChange}/>
        </div>
        <Stage
          width={clientWidth}
          height={stageHeight}
          onClick={this.hanldeStageClick}
          onMouseMove={this.handleMouseMove}
        >
          <Layer>
            {
              image ? (
                <Image
                  x={0}
                  y={0}
                  width={clientWidth}
                  height={stageHeight}
                  image={image}
                />
              ) : (
                <Text x={50} y={50} text="请选择一张图片以进行编辑" />
              )
            }
            {
              drawingRect && (
                <Rect
                  zIndex={100}
                  {...getPosition(drawingRect)}
                  stroke={'red'}
                  fill={'rgba(255,255,255, 0.5)'}
                />
              )
            }
            {
              areas.map((item, index) => {
                const pos = getPosition(item)
                const handleDragFuc = this.hanldeDrageFactory(index)
                return (
                  <Rect
                    key={`area-${index}`}
                    {...pos}
                    zIndex={10}
                    stroke={'yellow'}
                    fill={'rgba(0,0,0, 0.5)'}
                    draggable={true}
                    onDragStart={handleDragFuc}
                    onDragEnd={handleDragFuc}
                  />
                )
              })
            }
          </Layer>
        </Stage>
      </div>
    );
  }
}

const getPosition = ({ start, end }) => ({
  x: Math.min(start.x, end.x),
  y: Math.min(start.y, end.y),
  width: Math.abs(start.x - end.x),
  height: Math.abs(start.y - end.y)
});

export default App;



